* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html, body {
  height: 100%;
  font-size: 20px;
  font-family: Roboto, sans-serif;
  color: #707070;
  line-height: 1.4; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  transition: all .2s linear; }

.container {
  max-width: 1400px;
  width: 100%;
  margin: auto; }

.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column; }

.main {
  display: flex;
  flex-direction: column;
  flex: 1; }

.w-500 {
  width: 500px; }

.header {
  padding: 15px 0;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2); }
  .header .container {
    display: flex;
    justify-content: space-between; }
  .header .logo-nav-wrapper {
    display: flex; }
  .header .nav {
    margin-left: 80px; }
  .header .header-actions {
    display: flex;
    align-items: center; }
  .header .header-action {
    margin-right: 20px; }
    .header .header-action:last-child {
      margin-right: 0; }

.section {
  display: flex;
  padding: 100px 0;
  justify-content: center;
  align-items: center; }
  .section:nth-child(even) {
    background-color: #eeeeee; }

.hero-section {
  height: 65vh; }

.section-container {
  display: flex;
  margin: 0;
  flex-direction: column;
  align-items: flex-start; }
  .section-container.center {
    align-items: center; }

.section-buttons {
  display: flex; }
  .section-buttons .btn {
    margin-right: 20px; }
    .section-buttons .btn:last-child {
      margin-right: 0; }

.footer {
  padding: 100px 0;
  box-shadow: 0 -1px 10px rgba(0, 0, 0, 0.2); }
  .footer .container {
    display: flex;
    justify-content: space-between; }
  .footer .logo-nav-wrapper {
    display: flex; }
  .footer .nav {
    margin-left: 80px; }
  .footer .footer-actions {
    display: flex;
    align-items: center; }
  .footer .footer-action {
    margin-right: 20px; }
    .footer .footer-action:last-child {
      margin-right: 0; }

.logo {
  display: flex;
  align-items: center;
  color: inherit; }

.logo-image {
  margin-right: 20px;
  border-radius: 100%; }

.nav {
  display: flex; }
  .nav ul {
    display: flex;
    align-items: center; }
  .nav li {
    list-style: none;
    margin-right: 20px; }
    .nav li:last-child {
      margin-right: 0; }
  .nav a {
    color: inherit; }

.btn {
  display: inline-block;
  padding: 10px 20px;
  text-align: center;
  color: inherit;
  border-radius: 10px; }
  .btn-primary {
    background-color: #666666;
    color: white; }
  .btn-secondary {
    background-color: #DDDDDD; }
  .btn-white {
    background-color: white; }

button.btn {
  border: none;
  cursor: pointer; }

.grid {
  display: flex; }
  .grid.col-gap-1 .col {
    margin-right: 10px; }
  .grid.col-gap-2 .col {
    margin-right: 20px; }
  .grid.col-gap-3 .col {
    margin-right: 40px; }
  .grid.col-gap-4 .col {
    margin-right: 60px; }
  .grid.col-gap-5 .col {
    margin-right: 80px; }
  .grid .col {
    flex: 1; }
    .grid .col:last-child {
      margin-right: 0; }
    .grid .col.center {
      display: flex;
      justify-content: center;
      align-items: center; }

.card {
  display: flex;
  padding: 40px;
  flex-direction: column;
  align-items: center;
  color: inherit;
  background-color: white;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2); }

.card-heading {
  margin-bottom: 40px;
  text-align: center; }

.card-image {
  margin-bottom: 40px;
  border-radius: 100%; }

.card-text {
  text-align: center; }

.form {
  display: flex;
  width: 1200px;
  flex-direction: column;
  align-items: center; }

.form-field {
  display: block;
  width: 100%;
  margin-bottom: 40px;
  padding: 10px 20px;
  border: none;
  outline: none;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px; }
  .form-field-textarea {
    resize: none; }
