.section {
  display: flex;
  padding: ($spacer * 5) 0;
  justify-content: center;
  align-items: center;

  &:nth-child(even) {
    background-color: $bg-dark; } }

.hero-section {
  height: $hero-section-height; }

.section-container {
  display: flex;
  margin: 0;
  flex-direction: column;
  align-items: flex-start;

  &.center {
    align-items: center; } }

.section-buttons {
  display: flex;

  .btn {
    margin-right: $spacer;

    &:last-child {
      margin-right: 0; } } }
